n Nacional. En esta mesa se definen los municipios en los que se intervendrá y las acciones conjuntas que se implementarán para el cumplimiento de los propósitos de la ruta de alerta. Así mismo, se busca integrar las regionales del ICBF y Prosperidad Social con las secretarías de educación departamentales; y en los municipios la alcaldía municipal a través del enlace de Familias en Acción y la Secretaría de Educación Municipal, los rectores de las instituciones educativas y los comisarios de familia. ")]),_c('p',{staticClass:"mb-3"},[_vm._v("Esta ruta se desarrolló desde finales del año 2017 en 60 municipios que fueron seleccionados a partir de fuentes de información con las cuales se identificaron las variables territoriales que permitieran seleccionar los municipios con mayor riesgo en trabajo infantil y otro tipo de vulneración de derechos. Durante el segundo semestre del 2018, se realizó el seguimiento a la implementación de la Ruta, para determinar la pertinencia de esta, las condiciones que se requieren para lograr los resultados esperados y realizar el escalamiento a municipios adicionales. El seguimiento mostró un impacto positivo de las acciones propuestas para mejorar los incumplimientos dada la articulación de acciones entre diferentes entidades, la promoción de la oferta y la retroalimentación de las acciones que se desarrollan para dar respuesta a los diferentes casos, sin embargo, una de las falencias encontradas fue la falta de formación en temas relacionados con la protección integral de derechos de niños, niñas y adolescentes y la activación oportuna de esta ruta. ")]),_c('p',[_vm._v("Para superar esta dificultad, se elaboró un programa de formación que contó con el apoyo de las entidades de la Mesa Nacional de la Estrategia de Alertas y de UNICEF que aportó recursos económicos y humanos para materializarlo. El programa busca brindar herramientas conceptuales y metodológicas a los actores locales que participan en la ruta de alertas de Familias en